document.addEventListener('DOMContentLoaded', function() {
    scrollNav();
    navegacionFija();
});


function navegacionFija() {

    const navBar = document.querySelector('.nav');

    const options = {
        //root
        //rootMargin
        threshold: .5,
    }
    function callback(entries, observer) {
        if(entries[0].isIntersecting) {
            navBar.classList.remove('nav-fixed');            
        } else {
            navBar.classList.add('nav-fixed');            
        }
    }

    const observer = new IntersectionObserver(callback, options);

    // Elemento a observar
    observer.observe(document.querySelector('.contenido-header'));
}

function scrollNav() {
    //Obtiene todos los elementos de tipo a que en su atributo href exista en cualquier parta el símbolo # esto es para omitir el enlace de whattsapp ya que este redirecciona a una web externa y no a una sección de la misma página.
    const enlaces = document.querySelectorAll('a[href*="#"]');    

    enlaces.forEach( function( enlace ) {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();
            
            
            const seccion = document.querySelector(e.target.attributes.href.value);
            seccion.scrollIntoView({
                behavior: 'smooth'
            });
            
            
        });
    });
}

//Esta función se ejecuta sin llamarla en código, si no, cuando se presiona el botón menu.
//Al no llamarla en el  DOMContentLoaded se evita que al cargar la página este menú se abra automáticamente
function sideBarActive(){

    let siteNav = document.getElementById('navigation');
        siteNav.classList.toggle('site-nav-open');
    /* Cambia el botón de menu a x */
    let menuOpen = document.getElementById('menu-toggle');
        menuOpen.classList.toggle('menu-open');    
        
}

//Validación de form 

function limpiarAdvertencia(){
    var errores = document.getElementsByClassName("error");
    for(var i = 0; i < errores.length; i++){
      errores[i].innerText = "";
    }
}
  
function validar(form) {

    limpiarAdvertencia()

    //Validación name
    if (form.name.value.trim().length == 0) {

        document.getElementById("errorname").innerText = "Campo obligatorio";
        form.name.focus();

        return false;
        
    }
    //Validación email
    if (form.email.value.trim().length == 0) {
            
        document.getElementById("erroremail").innerText = "Campo obligatorio";
        
        form.email.focus();
        return false;
        
    }
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!re.test(form.email.value)) {
        document.getElementById("erroremail").innerText = "El email no es válido asegúrate de que tenga un formato como éste: ejemplo@email.com ";
        form.email.focus();
        return false; 
    }

    if (form.message.value.trim().length == 0) {

        document.getElementById("errormessage").innerText = "Déjanos un mensaje para poder ayudarte";
        form.message.focus();

        return false;
        
    }

}